import React from "react";
import { graphql, Link } from "gatsby";
import styled from "styled-components";
import Layout from "../components/layout";

const BlogContainer = styled.div``;

const BlogPost = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 1024px;
  margin: 0 auto;
`;

const BlogHeader = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
`;

const BlogTitle = styled.h1``;

const BlogDate = styled.h5``;

const BlogPostContent = styled.div`
  min-width: 960px;
`;

const BlogPagination = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 3rem;
  a {
    text-decoration: none;
  }

  a:hover {
    color: red;
  }
`;

export default function Template({
  data,
  pageContext, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;
  const { next, previous } = pageContext;

  return (
    <Layout>
      <BlogContainer>
        <BlogPost>
          <BlogHeader>
            <BlogTitle>{frontmatter.title}</BlogTitle>
            <BlogDate>{frontmatter.date}</BlogDate>
          </BlogHeader>
          <BlogPostContent dangerouslySetInnerHTML={{ __html: html }} />
          <BlogPagination>
            {previous ? (
              <Link to={previous.frontmatter.path}>
                {`<- ${previous.frontmatter.title}`}
              </Link>
            ) : (
              <span></span>
            )}
            {next ? (
              <Link
                to={next.frontmatter.path}
              >{`${next.frontmatter.title} ->`}</Link>
            ) : (
              <span></span>
            )}
          </BlogPagination>
        </BlogPost>
      </BlogContainer>
    </Layout>
  );
}

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "YYYY년 MM월 DD일")
      }
    }
  }
`;
